@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
footer{
  background-color: lightslategray;
  color: whitesmoke;
  font-family: 'Raleway', sans-serif;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-weight: bold;
}
.homeH{
  display: inline-block;
  font-family: 'Raleway', sans-serif;
  font-weight: bold;
  font-size: 1.5em;
}

.headerL{
  position: fixed;
  top: 0;
  left: 0.5em;
}

.headerR{
  position: fixed;
  top: 0;
  right: 0.5em;
}
.entire-box{
  display: inline-block;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
}

.formL, .title{
  font-family: 'Raleway', sans-serif;
  text-align: center;
}

.title{
  color: lightyellow;
  font-size: 3em;
  font-weight: bold;
  margin-bottom: 0.25em;
  text-decoration: underline;
}

.formL>label{
  display: block;
  font-weight: bold;
  font-size: 1.5em;
  color: black;
}

.formL>input{
  font-family: 'Raleway', sans-serif;
  font-size: 1em;
  width: 20em;
  height: 1.8em; 
  margin: 0em 0em 1.5em 0em;
  border-radius: 0.25em;
}

.formL>button{
  border-radius: 1em;
  font-family: 'Raleway', sans-serif;
  font-size: 2em;
  padding: 0.625em 1.25em 0.625em 1.25em;
  margin: 0.625em; 
}

.formL>button:hover{
  background-color: greenyellow;
  border-color: greenyellow;
}

.splash-page{
  background: url(/static/media/splashbg.69878743.PNG) repeat center center fixed;
  min-height: 100%;
  min-width: 1024px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.loginH{
  font-family: 'Raleway', sans-serif;
  text-align: center;
  font-size: 5em;
  font-weight: bold;
  margin: 0.475em;
  color: whitesmoke;
  text-decoration: underline;
}


